.todo-item{
    padding: 16px 5px;
    border-bottom: 1px dashed #bbb;
    display: -ms-flexbox;
    display: flex;
  }
  
  .todo-item:hover{
    background: grey;
  }
  
  .item-name{
    -ms-flex-preferred-size: 0;
        flex-basis: 0;
    -ms-flex-positive: 9;
        flex-grow: 9;
  }
  
  .item-remove{
    -ms-flex-preferred-size: 0;
        flex-basis: 0;
    -ms-flex-positive: 1;
        flex-grow: 1;
    text-align: right;
    color: #0090ff;
    font-weight: bold;
    cursor: pointer;
  }


