body{
  background: #222;
  margin: 20px;
  font-family: tahoma;
}

#todo-wrapper{
  width: 80%;
  margin: 0 auto;
  background: grey;
  border-top: 3px solid #0090ff;
  padding: 10px;
  border-radius: 2px;
  -webkit-box-shadow: 1px 3px 5px rgba(0,0,0,0.2);
          box-shadow: 1px 3px 5px rgba(0,0,0,0.2);
  color: #333;
}

#todo-wrapper p{
  color: #222;
  font-style: italic;
}

#todo-list ul{
  list-style-type: none;
  padding: 0;
}


